import { Row, Col, Table, Slider, Typography } from 'antd'
import { useState } from 'react';
import Header from "./Components/Header";
import {columns} from "./columns";
import {pokemons} from './data'

const dataSource = pokemons.map(item => ({...item, key: item.id}))

function App() {
  
  const [rows, setRows] = useState(5)

  return (
    <>    
      <Header /> 
      <Row>
      <Col xs={24} md={{span: 12, offset: 6}}>
      <Typography.Title level={4}>Количество Покемонов на странице</Typography.Title>
      <Slider min={1} max={12} defaultValue={rows} onChange={setRows} />
      <Table
            dataSource={dataSource}
            columns={columns}            
            pagination={{
              pageSize: rows
              // defaultPageSize: '3',
              // showSizeChanger: true,
              // pageSizeOptions: ['3','5' ,'10', '20']              
            }}
        /> 
      </Col> 
       </Row> 
    </>
  );
}

export default App;
